<template>
  <div class="page">
    <div class="head">
      <span @click="queryRandomPoetry">刷新</span>
    </div>
    <div class="poetry">
      <div v-if="data.loading" class="loading">加载中..</div>
      <div class="rhythmic">{{data.poetry.rhythmic}}</div>
      <div class="author">{{data.poetry.author}}</div>
      <div class="content">
        {{content}}
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, onMounted, computed } from '@vue/composition-api'
import fly from 'flyio'
// import axios from 'axios'
// const fly = new Fly()
export default {
  setup (props, context) {
    const data = reactive({
      poetry: {},
      loading: true
    })
    const content = computed(() => {
      let result = ''
      if (data.poetry && data.poetry.content) {
        result = data.poetry.content.split('>>')[0]
      }
      return result
    })
    const queryRandomPoetry = () => {
      // axios.get('https://hapi-peotry.herokuapp.com/api/peotry/random').then(response => {
      //   console.log('res by axios', response)
      // })
      // console.log('index base url', process.env.VUE_APP_URL)
      data.loading = true
      fly.get('https://hapi-peotry.herokuapp.com/api/peotry/random').then(response => {
        console.log(response)
        let res = response.data
        if (res.statusCode === 200 && res.data) {
          data.loading = false
          data.poetry = res.data[0] || {}
        } else {
          data.loading = false
        }
      })
      // context.root.$api.getPoetryRandom().then(response => {
      //   if (response.statusCode === 200 && response.data) {
      //     data.loading = false
      //     data.poetry = response.data[0] || {}
      //   } else {
      //     data.loading = false
      //   }
      // })
    }
    onMounted(() => {
      queryRandomPoetry()
    })
    return {
      data,
      content,
      queryRandomPoetry
    }
  }
}
</script>
